<template>
  <div class="container">
    <div class="wraper_name">
     <div class="back-btn">
      <backBtn @click="$router.go(-1)" />
    </div>    
    <form @submit="handleSubmit">
      <inputText :warna="warna" idName="password_lama" textLabel="Password lama" placeholder="Masukan password lama" types="password" @data-input="handleLama" forgot="right"/> 
      <inputText :warna="warna" idName="Password_baru" textLabel="Password baru" placeholder="Masukan password baru" types="password" @data-input="handleBaru"/>
      <inputText :warna="warna" idName="password_baru_ke2" textLabel="Masukan kembali password baru" placeholder="Masukan kembali Password baru" types="password" @data-input="handleBaru2"/>
    </form>
    </div>
    <p class="danger-text">{{ pesan }}</p>
     <sendBtn :warna="warna.primary" v-if="active && cocok" @click="handleClick"/>
     <sendBtn :warna="warna.text" v-else/>
    </div>
</template>

<script>
import inputText from '../components/etc/inputText.vue'
/*{
idName,
textLabel,
warna,
placeholder
}*/
import backBtn from '../components/mats/backBtn.vue'
import sendBtn from '../components/mats/sendBtn.vue'

  export default{
    name: 'changePassword',
    data() {
      return {
        passwordLama: '',
        passwordBaru: '',
        passwordBaru2: '',
        active: true,
        cocok: false,
        pesan: '',
        token: localStorage.getItem('xtoken') ?? this.$swal({
          icon: 'error',
          title: 'Gagal!',
          text: 'Anda harus login dahulu untuk melanjutkan aksi!'
        }).then(res => {
          if(res.isConfirmed) {
            this.$route.push("/login")
          }
        }),
      }
    },
    components: {
      inputText,
      backBtn,
      sendBtn
    },
    methods: {
      handleLama(data) {
        this.passwordLama = data;
        this.validatornya()
      },
      handleBaru(data) {
        this.passwordBaru = data;
        this.validatornya()
      },
      handleBaru2(data){
        this.passwordBaru2 = data;
        this.validatornya()
      },
      validatornya() {
        this.cocok = true;
      },
      handleClick() {
        this.$swal.fire({
          title: 'Apakah Anda yakin?',
          text: 'Password awal anda akan di ganti dengan yang baru selamanya, pastikan anda mengingatnya!',
          icon: 'info',
          showDenyButton: true,
          confirmButtonText: 'Ya',
          denyButtonText: 'Tidak'
        }).then(async res => {
          if(res.isConfirmed) {
            try {
              const response = await this.axios.put('/Authentification/changePassword', {
                old_password: this.passwordLama,
                new_password: this.passwordBaru,
                confirm_new_password: this.passwordBaru2
              }, {
                headers: {
                  Authorization: `Bearer ${this.token}`
                }
              })
              this.$notify({
                title: response.data.response.message,
                text: 'Kata Sandi berhasil di ganti.'
              })
            } catch(e){
              alert(e)
            }
          } else if (res.isDenied) {
            this.$notify({
              title: 'Upss!',
              text: 'Kata sandi anda masih tetap yang lama',
              type: 'warn'
            })
          }
        })
      }
    },
    props: {
      warna: {
        type: Object,
        required: true
      }
    },
    created() {
      this.$emit('hilang-navbar', false)
    }
    
  }
</script>

<style scoped>
.container {
  position: relative;
  width: 100%;
  padding: 0.3rem 0.5rem;
}
.wraper_name {
  max-width: 800px;
  margin: 0 auto;
}
.danger-text {
  color: #a32222;
  font-size: 0.6rem;
}
</style>
